import React from 'react'
import styled from "styled-components";
import { sizes, theme } from "../GlobalStyle";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faRocket,faListUl,
     faPhotoVideo,
      faEnvelopeOpenText,
       faHeart,
    faMousePointer
    } from '@fortawesome/free-solid-svg-icons'
import {IconDiv,CardIc,OtherServices} from "./IconCardStyled"



export default function IconDivN() {
    return (
        <IconDiv>
            <CardIc>
            <FontAwesomeIcon icon={faRocket} color={"red"} size="4x" />
            <h2>SEO</h2>
              <p>          We Aim For Content Quality That Will Engage, Entertain, And Ultimately Drive Conversations. We Also Aim To Make That Content Available And Visible To A Broad Audience.
                   We Choose Organic, Focus On Keyword Placement And Picture It.</p>
            </CardIc>
            {/* --icon div -- */}
            <CardIc>
            <FontAwesomeIcon icon={faMousePointer} color={"red"} size="4x" />
            <h2>PPC</h2>
              <p>   We use fresh, significantly impactful content that is easy to implement and use wide variety of useful range of ad channels to search and socialize by keeping it fast and simple. We dynamically change your loading page for each audience type. We reverse engineer your conversion funnel. We take 
                  it to the net level with lookalikes. We retarget by long tail keywords.</p>
            </CardIc>
            {/* --icon div -- */}
            <CardIc>
            <FontAwesomeIcon icon={faPhotoVideo} color={"red"} size="4x" />
            <h2>Content Marketing</h2>
              <p>  We use infographics, include videos and images and focus on solving a customer’s question with in-depth content. We create 100% original content. We enrich the content by creating content that benefits the reader and is different from what they will read on ten other blogs.
  We read analytic reports and ensure that the content will pertain to different readers. We reframe the content to best suit segmented audiences. We also use multiple distribution methods to gain access to target audiences. We optimize everything for ease of search of readers.</p>
              
            </CardIc>
            {/* --icon div -- */}
            <CardIc>
            <FontAwesomeIcon icon={faEnvelopeOpenText} color={"red"} size="4x" />
            <h2>E-mail marketing</h2>
              <p>We generate responsible list building and maintain it. We also segment the relevant content providing a good layout and design and focus on strong call-to-action plans.
              Website design and Development:
  We provide a fresh look to engage readers and make the website interactive by providing display of your products and services using high quality images and also establish you as a source of information in your industry by video marketing. We offer value to customer and bolster your reputation. We focus on making the website content accessible and mobile-friendly.</p>
              
            </CardIc>
            {/* --icon div -- */}
            <CardIc>
            <FontAwesomeIcon icon={faHeart} color={"red"} size="4x" />
            <h2>Social media marketing:</h2>
              <p>  We use smart targeting options to help your posts find the audiences most likely to respond to your brand. We use geotargeting to expand your reach of your local efforts and generate website traffic that is more relevant to your market and goals. We remarket your posts by tracking customer through cookie placement so you can target specific ads to specific people, based on the pages that they have viewed on the website. We distribute your content across sites where your users spend their time and target your efforts to readers who are interested in content related to your brand or service by behavioral advertising.We also facilitate guest blogging for you to have a new pool of connected and engaged followers.</p>

            </CardIc>
             {/* --icon div -- */}


        </IconDiv>
     
    )
}
