import React from "react";
import styled from "styled-components";
import {sizes,theme} from "../../GlobalStyle";

const FTWrap = styled.div`

  display: grid;
  grid-template-columns:1fr;
  grid-template-rows: 1fr;
  // border-top:1px solid ${theme.black2};
  box-sizing:border-box;
  padding:1em 2em;
  color:${theme.white};
  background-color:${theme.black};
  h1{
    text-transform: capitalize;
    font-size:20px;
    color:${theme.red};
  }
  > div {
    padding:10px 20px;
    a{
      text-decoration:none;
      font-size:0.8em;
      cursor:pointer;
      color:${theme.white};
    }
    a:hover{
      color:${theme.red};
    }
  }
  @media (min-width: ${sizes.tablet}px) {
    grid-template-columns: repeat(2, 1fr);
    h1{
      font-size:24px;
    }
    > div {
      padding:10px 20px;
      a{
       
      font-size:0.8em;
    }
  }
`;
const FT = styled.div`
  /* border: 2px solid orange; */
  font-size: 22px;
  font-weight: 500;
  >div>div{
    padding-bottom:1em
  }
  
`;

const Footer = props => (
  <FTWrap>
    <FT className="FT FT_Root1">
      <h1>© Branding Man - All right reserved.</h1>
    </FT>
    <FT className="FT FT_Root2">
   
          <h1 >Get in touch with us</h1>
         <div>
          <div>

          <a href="https://www.google.com/maps/place/Branding+Man+-+Best+Digital+Marketing+Services,+Online+Marketing+and+Advertising,+Internet+Marketing+Agency,+SEO+Agency/@23.114788,72.5839546,15z/data=!4m5!3m4!1s0x0:0x2fd802a68595b892!8m2!3d23.114788!4d72.5839546"
          target="_blank">
            415/416 Orange Mall, Near Sharda Petrol Pump, Chandkheda, Ahmedabad - 382424
          </a>
          </div>
          <div>
          <a href="tel:+91 8200873938">+91 8200873938 </a>

          </div>
             <div>
             <a href="mailto:info@brandingman.in">info@brandingman.in</a>

             </div>
         </div>
          {/* <div>
          <p>
           
          </p>
          </div> */}
       

    </FT>
  </FTWrap>
);

export default Footer;
