import { sizes, theme } from "../../GlobalStyle";

import styled from "styled-components";

const Nav1 = styled.div`
.burger{ 
   
    cursor:pointer;
    display:block;
    >button{
        background-color:${theme.white};
        border:1px solid transparent;
    }
    button:focus{
        outline:none;
    }
}

box-sizing:border-box;
display:grid;
background-color:${theme.white};
width:100%;
grid-template-rows:1fr;
grid-template-columns:.5fr auto auto;
grid-gap:1em;
z-index:1100;
box-shadow:0px 2px 4px -1px rgba(0,0,0,0.2),
 0px 4px 5px 0px rgba(0,0,0,0.14),
  0px 1px 10px 0px rgba(0,0,0,0.12);
padding:0;
margin:0;
align-items:center;
justify-items:center;
// box-shadow:0px 1px 7px #00000033;

.icons_nav{
    display:none;
   
    >a{
    
        transition: 0.48s;
        align-items: center;
    }
}
.nav_a_p{
    font-size:1em!important;
    transition: all 300ms linear 0s;
   >a{ padding-left:10px;}
}
.logo{
    margin-left:10px;
}
img{
    
    height:70px;
    width:auto;
}
nav{
    height:inherit;
  }
ul{
    padding-inline-start:0;
    margin:0;
    display:grid;
    grid-template-rows:1fr;
    grid-template-columns:1fr ;
    font-size:1em;
    align-items:center;
    height:100%;

}
ul>li{
    height:inherit;
    // line-height:44px;
    text-align:center;
    svg{
        color:${theme.black2}
    }
    svg::hover{
        color:${theme.red}
    }
a{
    color:${theme.red};
    text-decoration:none;
    text-transform:capitalize;
    align-items: center;
    background-clip: text;
    -webkit-background-clip: text;
    // color: transparent;
    /* bg gradient */
    border-radius: 2px;
    // font-size: 12px;
    font-weight:800;
   
    user-select:none;
    /* margin-right: 2rem; */
    /* padding: 8px 19px; */
    z-index: 11;
   
}

a:visited{
    text-decoration:none;
    color:black;
}
a:hover{
    color:red;
    text-shadow:0 2px #c3292933;
}
list-style-type:none;
}


// nav1 media query
// tablet
@media (min-width: ${sizes.tablet}px) {
   

    grid-gap:20%;
    ul{
        grid-template-columns:2fr 1fr ;
    }
    // li,a{ 
    //     font-size:.9em;
    // }
    .nav_a_p{ 
       a{ font-size:1.2em;}
    }
    .icons_nav{
     
        
        display:hidden;
        // padding-top:5px;
        grid-template-rows:1fr;
        >*{padding-right:0.5em;}
       
    }
}
// tablet end
@media (min-width: ${sizes.desktop}px) {
    .burger{
        display:none;
    }
    grid-template-columns:.5fr auto ;
    grid-gap:4fr;
    >ul{
        grid-template-columns:1fr 0.8fr ;
        grid-gap:5em;
    }
    .icons_nav{
       
        display:grid;
        
        grid-template-columns:repeat(5,1fr);
        grid-template-rows:1fr;
        a>svg{
            border-radius: 14%;
      
        }
        a>svg:hover{
            color:red;
            // box-shadow: 1px 1px 5px grey;
        }
        >*{padding-right:4em;}
}
    
`

export { Nav1 };