import styled from 'styled-components'
import { sizes, theme } from "./GlobalStyle";

const FormDiv = styled.div`
display:grid;
grid-template-rows:auto;
grid-template-columns:1fr;
grid-gap:1em;
background:#fff;

.btn_div{
    padding:1em;
    // justify-content:center;
    // align-content:center;
}
.btn{
    border:0.5px solid grey;
    border-radius:10px;
    background:#fff;
    color:red;
    font-size:22px;
    // padding:10px;
    
}
input{
    box-shadow: inset 0px 1px 3px rgba(0, 0, 0, 0.2);
}
input:focus-within{
    box-shadow: inset 0px 1px 8px rgba(70, 133, 171, 0.43)
}
@media (min-width: ${sizes.desktop}px) {
    .btn_div{
        padding:0;
        align-items:left;
    }
   .btn{
       width:5em;
   }
  }

>div{
    display:grid;
    background:white;
    grid-template-rows:1fr;
    grid-template-columns:1fr; 
    grid-gap:0.2em;
    input{
        height:auto-fit;
        margin: 0;
        outline: 0;
        background-color: #fff;
        border: 1px solid #e6ecf0;
        border-radius: 3px;
        padding:1em;
        
    }
  
    }
}
`
export { FormDiv };