import styled from "styled-components";
import { sizes, theme } from "../GlobalStyle";

const IconDiv= styled.div`
margin:10px 10px;
box-sizing:border-box!important;
display:grid;
grid-template-rows:1fr;
grid-template-columns:1fr;
grid-gap:5em;
// border:2px solid yellow;
// *,*>*,*>*>*{
//     border:2px solid yellow;
// }
>div{
    -webkit-transition: all  .7s;
    transition: all  .7s;

  }
  >div:hover{
    background-color:#ffcbba54;
    transform: translateY(-10px);
    border:2px solid ${theme.red};
}

@media (min-width:${sizes.tablet}px){
    grid-template-columns:1fr 1fr;

}


`

const CardIc=styled.div`

cursor:pointer;
margin-top:10px;
padding:10px 5px;
box-sizing:border-box;
border-radius:10px;
border:2px solid ${theme.borderThin};
// box-shadow:0px 1px 9px 3px #999;
text-align: center;
h2{ text-transform:uppercase;
    padding:0;
    margin:0;
    margin-top:20px;
    font-size:1.5em!important;
    
}
p{   text-align:justify;
    font-size:0.9em;
    padding:0px 15px;
}
@media (min-width:${sizes.tablet}px){
    h2{ 
        font-size:1em!important;
}
p{ 
    padding:0 1em;
    font-weight:400;
    font-size:0.6em;
    line-height:1.6;
    

}
@media (min-width:${sizes.desktop}px){
    h2{ 
        font-size:0.8em!important;
}
p{
    font-size:0.5em!important;
}
}
`
// other services

const OtherServices=styled.div`
display:grid;
grid-template-rows:1fr;
grid-template-columns:1fr;
@media (min-width:${sizes.tablet}px){
    grid-template-rows:1fr;
grid-template-columns:1fr 1fr;
}
`



export {IconDiv,CardIc,OtherServices};