import React from 'react'
import styled from "styled-components";
import { sizes, theme } from "../GlobalStyle";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faRocket,
    faCode,
    faShippingFast,
    faClipboardList,
    faAd,
    faMobile,
    faShareAltSquare,
    faTasks,
    faEnvelopeOpenText,
    faSignal
} from '@fortawesome/free-solid-svg-icons';
// import {faSketch,faAppStoreIos} from "../layout/navbar/Navbar"


// styled--

const OtherTop= styled.div`
h2{
    color:${theme.red};
    text-transform: uppercase;
    padding: 0;
    margin: 0;
    margin-top: 20px;
    font-size: 1.5em!important;
    text-align:center;
}
margin:10px 10px;
box-sizing:border-box!important;
display:grid;
grid-template-rows:1fr;
grid-template-columns:1fr;
grid-gap:1em;

align-items:center;
// border:2px solid blue;
// *,*>*,*>*>*{
//     border:2px solid blue;
// }

//other services
// .other_services{
//     text-align:center;
// } 
@media (min-width:${sizes.tablet}px){
    

}
`


const OtherServices=styled.div`
display:grid;
grid-template-rows:1fr;
grid-template-columns:1fr;

box-sizing:border-box;
p{
    margin:0;
    padding:0;
}
>div>div{
  box-sizing:border-box;
    display:grid;
    grid-template-rows:1fr;
grid-template-columns:auto 1fr;
grid-gap:10px;
margin-bottom:5px;
padding:10px 20px;
-webkit-transition: all  .7s;
transition: all  .7s;
align-items:center;
  }


@media (min-width:${sizes.tablet}px){
    grid-template-rows:1fr;
grid-template-columns:1fr 1fr;
grid-gap:2em;
}
`



// styled--
export default function ListComp() {
    return (
   <OtherTop>
       <h2>List of Services</h2>
      <OtherServices>
      <div>
      <div>
          <FontAwesomeIcon icon={['fab', 'sketch']} color={"red"} size="m" />
<p>Website Designing</p></div>
<div>
          <FontAwesomeIcon icon={faCode} color={"red"} size="m" />
<p>Website Development</p></div>
<div> <FontAwesomeIcon icon={faShippingFast} color={"red"} size="m" />
<p>Website Optimization</p></div><div>
          <FontAwesomeIcon icon={faClipboardList} color={"red"} size="m" />
<p>Content Optimization</p></div><div>
          <FontAwesomeIcon icon={['fab',"app-store-ios"]} color={"red"} size="m" />
<p>App Development</p></div><div>
          <FontAwesomeIcon icon={['fab',"searchengin"]} color={"red"} size="m" />
<p>Search Engine Marketing</p></div>

</div>
<div>
      <div>
          <FontAwesomeIcon icon={faShareAltSquare} color={"red"} size="m" />
<p>Social Media Marketing</p></div>
<div>
          <FontAwesomeIcon icon={faMobile} color={"red"} size="m" />
<p>Mobile Marketing and Integration</p></div><div>
          <FontAwesomeIcon icon={faAd} color={"red"} size="m" />
<p>Placement of online Ads</p></div><div>
          <FontAwesomeIcon icon={faTasks} color={"red"} size="m" />
<p>Cross-Platform Marketing</p></div><div>
          <FontAwesomeIcon icon={faEnvelopeOpenText} color={"red"} size="m" />
<p>Email Marketing</p></div>
<div> <FontAwesomeIcon icon={faSignal} color={"red"} size="m" />
<p>Online Marketing</p></div>


</div>
      </OtherServices>
   </OtherTop>

)
}
