import React, { Component } from "react";
import Navbar from './components/layout/navbar/Index'
import Footer from './components/layout/footer/Index'
import LandingPage from "./components/landingPage/Index.js";
import SideBar from "./components/sideBar/SideBar";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {navToggle: false};

  }
  render() {
    const handleButton =(e)=>{
      e.preventDefault();
      !this.state.navToggle
              ? this.setState({ navToggle: true })
              : this.setState({ navToggle: false });
    }
    return (
      <div>
        {/* <SideBar/> */}
        <Navbar navToggle={this.state.navToggle}  handleButton={handleButton}/>
        {
          this.state.navToggle?<SideBar/> :(
            <div className="WrapperCSS">
          <div>{}</div>
          <LandingPage />
          <div>{}</div>
        </div>
          )
        }
        <Footer />
      </div>
    );
  }
}

export default App;
