import styled from "styled-components";
import { sizes, theme } from "../GlobalStyle";
import HandshakeColor from "../../assets/SVG/handshake-colour.svg";
import ChociesColor from "../../assets/SVG/choices-colour.svg";

const MainTop = styled.div`


// main margin
margin:1em 1em;
// main margin
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-gap:7em;
  font-size: 20px;
  /* *{
    border:1px solid red;
  }
  *>*{
    border:1px solid purple;
  }
  *>*>*{
    border:1px solid orange;
  } */

@media (min-width:${sizes.desktop}px) {
  // main margin
  margin:1em 5em;
  // main margin
  font-size: 30px;
  p{
    font-size:22px;
  }
}


`;

const CardBox1 = styled.div`
// --------------
.banner{
 width:80vw;
 object-fit: contain;
}

*,*:before, *:after{
  box-sizing:border-box;
  
}
.svgs{
  box-shadow:none!important;
}


h1,h2{
  font-family: 'Hind Siliguri', sans-serif;
  
}
h3{
  padding:0;
  margin:0;
  
  font-weight: 400;
  border-left: 2px solid red;
  padding-left: 10px;

}
h2{
  text-transform:capitalize;
}
box-sizing:border-box;
p{
  font-size:18px;
}
// --------------
display:grid;
grid-template-columns:1fr;
grid-template-rows:1fr 1fr;
grid-gap:20px;
img{
  -webkit-transition: width  .7s;
  transition: width  .7s;
}

h1{
   color: ${theme.red};
    display: inline;
    text-decoration: underline;
    text-decoration-color: ${theme.borderThin};
    text-transform:capitalize;
}
p{
  font-size: 24px;
    line-height: 36px;
    color: ${theme.black2};
    text-transform:capitalize;
}

>div{
   
    box-sizing:border-box;
    padding:10px 20px;
    background-color:#fff;
    justify-content: center;
    align-items: center;
    // box-shadow:0px 0.5px 9px 0px grey;
    // border:0.3px solid grey;
  }
  
  @media (min-width: ${sizes.tablet}px) {
    .banner{
      width:40vw;
      object-fit: contain;
     }
     img:hover{
  
      width:41vw;
    }
     grid-template-rows:1fr;
    grid-template-columns: repeat(auto-fit, minmax(30%, 1fr));
    // grid-template-columns: 1fr;
    >div{
    box-sizing:border-box;
    
  }
  }
`;


const CardBox2 = styled.div`
display:grid;
grid-template-columns:1fr;
grid-template-rows:1fr;
grid-gap:10px;
h1{
  color: ${theme.red};
   display: inline;
   text-decoration: underline;
   text-decoration-color: ${theme.borderThin};
   text-transform:capitalize;
   text-align:center;
}
p{
 font-size: 24px;
   line-height: 36px;
   color: ${theme.black2};
   text-transform:capitalize;
}

>div{
  // box-shadow:0px 0.5px 9px 0px grey;
    box-sizing:border-box;
    padding:10px 20px;
    background-color:#fff;
    // border:0.3px solid grey;
  }
  
  @media (min-width: ${sizes.desktop}px) {
    
    /* grid-template-columns: repeat(auto-fit, minmax(30%, 1fr)); */
    >div{
    box-sizing:border-box;
    
  }
  }
`;


const Div2 = styled.div`
  background: url(${HandshakeColor}) no-repeat;
  align-content: center;
  background-size: contain;
  width: 100%;
  height: 100%;
  @media (min-width: ${theme.desktop}px) {
    height: 100%;
    width: 50%;
  }
`;
const Div3 = styled.div`
  background: url(${ChociesColor}) no-repeat;
  background-size: contain;
  width: 100%;
  height: 100%;
  @media (min-width: ${theme.desktop}px) {
    height: 100%;
    width: 50%;
  }
`;
export { MainTop, CardBox1, CardBox2, Div2, Div3 };
