import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import styled from "styled-components";
import { sizes, theme } from "../GlobalStyle";



const IconsNav=styled.div`
display: grid;
grid-gap: 3em;
grid-template-columns: 1fr 1fr 1fr;
grid-auto-rows: auto;
align-items:center;
justify-items:center;
   >a{
     transition: 0.48s;
    align-items: center;
    }
    a>svg:hover{
        color:red;
        // box-shadow: 1px 1px 5px grey;
    }
    
    @media (min-width:${sizes.tablet}px){

      grid-template-columns: 1fr 1fr;
    }
    @media (min-width:${sizes.desktop}px){
     display:hidden;   
    }
`

export default function SocialIcon() {
    return (
        <IconsNav >
             <a href="https://www.facebook.com/brandingman.in" target="_blank">
                <FontAwesomeIcon icon={['fab', 'facebook-f']} color="#ff253a"  size="3x" /></a>
             <a href="https://www.instagram.com/brandingman_in/" target="_blank" >
                <FontAwesomeIcon icon={['fab', 'instagram']} color="#ff253a"   size="3x" /></a>
             <a href="https://twitter.com/brandingman_in"target="_blank">
                <FontAwesomeIcon icon={['fab', 'twitter']} color="#ff253a"     size="3x" /></a>
             <a href="https://www.linkedin.com/company/brandingman/" target="_blank">
                <FontAwesomeIcon icon={['fab', 'linkedin-in']} color="#ff253a" size="3x" /></a>
             <a href="https://in.pinterest.com/brandingman_in/" target="_blank">
                <FontAwesomeIcon icon={['fab', 'pinterest']} color="#ff253a"   size="3x" /></a>
          
             <a href="https://g.page/BrandingMan" target="_blank">
                <FontAwesomeIcon icon={['fab', 'google']} color="#ff253a"   size="3x" /></a>
          
              
              </IconsNav>
    )
}
