import React from "react";
import {FormDiv} from "./formStyled";
// import user from "../data"
const user=[];

class ContactForm extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        id:"",
      name:"",  
      email:"",  
      phone_no:"",  
      msg:""  
      
      };
  
      this.handleChange = this.handleChange.bind(this);
      this.handleChangePhone = this.handleChangePhone.bind(this);
      this.handleSubmit = this.handleSubmit.bind(this);
    }
    handleChangePhone = e => {
      const { value, maxLength } = e.target;
      const phone_no = value.slice(0, maxLength);
  
      this.setState({
        phone_no
      });
    };
  
  
    handleChange(e) {
      this.setState({
        [e.target.name]: e.target.value
      })
    }
  
    handleSubmit(event) {
      
      event.preventDefault();
      // localStorage.setItem('data',JSON.stringify(this.state));
      const userAdd= user.concat(Object.values(this.state))
      const myJSON = JSON.stringify(userAdd);
      localStorage.setItem("testJSON", myJSON);
      console.log("myjson",myJSON)
    }
  
    render() {
      return (
        <form onSubmit={this.handleSubmit}>
          <FormDiv>
         <div>
         <label>
            Name:
          
            </label>
            <input type="text" name="name" value={this.state.name} onChange={this.handleChange} required />
            
         </div>
            {/* <label htmlFor="phone"> <PhoneInput country={'in'} name="phone" value={this.state.phone} onChange={phone=>this.setState({phone})}/></label> */}
         
           <div>
           <label htmlFor="email">Email:</label>
          <input type="email" name="email" value={this.state.email} onChange={this.handleChange} required />
          
           </div>
           <div>
           <label htmlFor="phone_number">Phone Number:</label>
          <input type="number" maxLength = {10} name="phone_no" value={this.state.phone_no} onChange={this.handleChangePhone} required />
          
           </div>
           <div>
           <label htmlFor="message">Message:</label>
          <input type="text" name="msg" value={this.state.msg} onChange={this.handleChange} required />
             
           </div>
       <div className="btn_div">
       <input className="btn" type="submit" value="Submit" />
       </div>  
          </FormDiv>
         
           {/*  */}
          
        </form>
      );
    }
  }

  export default ContactForm;