import { createGlobalStyle } from "styled-components";


const sizes = {
  huge: 1440,
  gaint: 1172,
  desktop: 1080,
  tablet: 768,
  phone: 376
};

// red: "gold",
// redopa: "#f9e684",
const theme = {
  red:"red",
  gold: "gold",
  goldbg: "#fff9d9",
  goldopa: "#f9e684",
  golddark: "#d5ad18",
  alinks: "#00f",
  white: "#fff",
  whiteHover: "#ececec42",
  whiteoff: "#f9f9f9",
  black: "#000",
  black2: "#2d2d2d",
  blackhover: "#2a2a2a",
  borderlight: "#bebebe",
  borderThin: "#efefef",
  cardbg: "#a5a5a5",
  purpleDark: "#4527a0",
  purpleLight: "#9575cd",
  flowerBlueDark: "#263c63",
  darkModeBlack1: "#1a1a1a",
  darkModeBorder: "#313131"
};

const GlobalStyle = createGlobalStyle`
html{
box-sizing:border-box;
}
*,*:before, *:after{
  box-sizing:border-box;
  
}
body { 
  
  transition: all 200ms ease-in-out;
  min-width: 360px;
  /* background-image:linear-gradient(34deg,#B5D43C,#B5D43C); */
  background-color:${theme.whiteoff};
  box-sizing: border-box;
   /* overflow-x: hidden; */
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
  Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size:16px

}

.App2 {
  /* //Universal Wrapper
  this is main wrapper around whole app */
  display: grid;
  /* grid-template-columns: 2% 2fr 2%; */
  grid-template-columns: 1fr;
  grid-row-gap: 2rem;
  background-color: inherit;
  text-align: center;
  position: relative;
  margin: 0 auto;
 
}
`;

export { sizes, theme, GlobalStyle };
