import React from "react";
import { Nav1 } from "./NavStyled_2";
import { sizes, theme } from "../../GlobalStyle";
import BR from "../../../assets/images/branding_man.jpg";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPhoneAlt, faBars } from '@fortawesome/free-solid-svg-icons'
import { fab, faSketch } from '@fortawesome/free-brands-svg-icons'
import {faAppStoreIos,
   faFacebookF,
   faInstagram,
   faTwitter,
   faLinkedinIn,
   faPinterest,
  faSearchengin,
faGoogle } from '@fortawesome/free-brands-svg-icons' 
import { library } from '@fortawesome/fontawesome-svg-core'
library.add(faFacebookF,
  faInstagram,
   faTwitter,
   faLinkedinIn,
   faPinterest,
   faSketch,
   faAppStoreIos,
   faSearchengin,
   faGoogle); 
   

// export {faSketch,faAppStoreIos};

class Navbar extends React.Component {

  render() {
  
    return (

      <Nav1>
        <div className="logo">
          <a href="/"><img src={BR} alt="logo" /></a>
        </div>
        {/* <div className="nav_list"> */}
          <nav>
            <ul>
              <li className="nav_a_p">
              <FontAwesomeIcon icon={faPhoneAlt} color={"red"} size="lg" />
              <a href="tel:+91-8200873938" title="Phone" > 
              
										+91 8200 873 938
										</a>
              </li>
              
              <li className="icons_nav">
             <a href="https://www.facebook.com/brandingman.in" target="_blank">
                <FontAwesomeIcon icon={['fab', 'facebook-f']} color="red"  size="2x" /></a>
             <a href="https://www.instagram.com/brandingman_in/" target="_blank" >
                <FontAwesomeIcon icon={['fab', 'instagram']} color="red"   size="2x" /></a>
             <a href="https://twitter.com/brandingman_in"target="_blank">
                <FontAwesomeIcon icon={['fab', 'twitter']} color="red"     size="2x" /></a>
             <a href="https://www.linkedin.com/company/brandingman/" target="_blank">
                <FontAwesomeIcon icon={['fab', 'linkedin-in']} color="red" size="2x" /></a>
             <a href="https://in.pinterest.com/brandingman_in/" target="_blank">
                <FontAwesomeIcon icon={['fab', 'pinterest']} color="red"   size="2x" /></a>
             {/* <FontAwesomeIcon icon={faInstagram} color={"red"} size="m" /> */}
              
              </li>
             
            </ul>
            
          </nav>
          <div className="burger"> 
            <button  onClick={this.props.handleButton} type="button" >
          <FontAwesomeIcon icon={faBars} color={"red"} size="2x"/>
          </button></div>
        {/* </div> */}
      </Nav1>
    )
  }
}
export default Navbar;
